.about {
  background-color: rgba(0, 0, 0, 0.88);
  width: 100%;
  box-sizing: border-box;

  .about_wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-top: 6rem;
    padding-bottom: 1rem;

    .about_image {
      position: relative;
      display: inline-block;

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.7);
        opacity: 0.5
      }
      
      img {
        display: block;
        max-width: 100%;
        height: auto;
      }

      .about_image_btn {
        position: absolute;
        top: 80%;
        left: 35%;

        button {
          border: none;
          text-transform: uppercase;
          color: white;
          background-color: #56b26f;
          border-radius: 25px;
          padding: 20px 50px;
          cursor: pointer;
      
          letter-spacing: 2px;
          font-family: "Inter", sans-serif;
          font-weight: 800;
          transition: color 0.3s ease;

          z-index: 5;
        }
      }
    }

    .about_text {
      p {
        font-size: 20px;
        width: 80%;
        margin-bottom: 1rem;
      }

      .about_title {
        h1 {
          font-size: 48px;
          padding-bottom: 1rem;
        }
      }
    }
  }
}
